<!--学校课表-->
<template>
	<div class="school">
		<div class="title">学校课表</div>
		<div class="container">
			<el-row :gutter="20">
				<el-col :span="4">
					<div class="school-nav">
						<div class="title">学校选择</div>
						<div class="select-school">
							<el-scrollbar style="height:100%">
								<syncTree @selectSchool="handleSelectSchool" :treeData="treeData" :defaultProps="defaultProps"></syncTree>
							</el-scrollbar>
						</div>
					</div>
				</el-col>
				<el-col :span="20">
					<div class="content">
						<div class="top">
							<div class="title">{{schoolValue}}</div>
							<timeSelect class="time-select" @time-change="timeSelected"></timeSelect>

						</div>
						<syn-calendar :tableData="lists" :type="4" :current="date"></syn-calendar>
					</div>
				</el-col>
			</el-row>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				treeData: [],
				defaultProps: {
					children: 'children',
					label: 'displayName',
					value: 'id'
				},
				schoolValue: '',
				schoolId: "1192260615721259009",
				date: new Date(),
				lists: []
			}
		},
		components: {
			timeSelect: () => import("@/components/time-selected"),
			synCalendar: () => import("@/components/calendar"),
			syncTree: () => import("@/components/sync-tree"),
		},
		created() {
			this.getSchool();
			this.getRoomclass();
		},
		methods: {
			/* getSchool */
			getSchool() {
				const params = {
					level: 0,
					onlyOrg: 1,
					parentId: 0,
					status: 0
				}
				this.$api.institutions(params).then(res => {
					this.treeData = res.data;
				})
			},
			/* 选中学校id */
			handleSelectSchool(val) {
				this.schoolId = val.schoolId;
				this.schoolValue = val.schoolValue;
			},
			getRoomclass() {
				const params = {
					schoolId: this.schoolId,
					date: this.date
				}
				this.$api.getRoomclass(params).then(res => {
					console.log(res);
					this.lists = res.data;
					this.lists.map(v => {
						v.date = this.getWeekDay(v.courseDate)
					})
				})
			},
			getWeekDay(params) {
				let time = new Date(params).getDay()
				return time == 0 ? 7 : time
			},
			timeSelected(value) {
				this.date = value;
				this.getRoomclass();
			}
		}
	}
</script>
<style lang="scss" scoped>
	.school {
		padding: 32px;
		height: 100%;
	}

	.school>.title {
		color: #55595C;
		font-weight: bold;
		line-height: 1
	}

	.container {
		padding-top: 20px;
		height: calc(100% - 34px);

		.school-nav {
			background: #fff;
			border-radius: 6px;
			padding: 23px;
			height: 700px;

			.title {
				color: #55595C;
				font-weight: bold;
				margin-bottom: 23px;
				line-height: 1;
			}

			.select-school {
				height: calc(100% - 37px)
			}
		}

		.content {
			min-height: 100%;
			background: #fff;
			border-radius: 6px;
			padding: 23px;
			padding-top: 7px;

			.top {
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-bottom: 7px;
			}

			.top .title {
				color: #55595C;
				font-weight: bold;
			}
		}
	}
</style>
